import React, { useEffect } from "react";
import IndexPage from "./IndexPage";
import "swiper/swiper.scss";
import "swiper/components/pagination/pagination.scss"
import "./assets/scss/main.scss";
import { BrowserRouter as Router, Switch, Route } from 'react-router-dom'
import { Smoke } from './pages/constants/smoke'

function App() {
    useEffect(() => {
        const smoke = new Smoke();
        smoke.update();
    }, [])
    return (
        <Router>
            <div className="app">
                <Switch>
                    <Route path="/libresse/" exact>
                        <IndexPage page={'menu'} />
                    </Route>
                    <Route path="/rules">
                        <IndexPage page={'rules'} />
                    </Route>
                    <Route path="/libresse/rules_full">
                        <IndexPage page={'rules_full'} />
                    </Route>
                    <Route path="/libresse/prizes">
                        <IndexPage page={'prizes'} />
                    </Route>
                    <Route path="/libresse/presents">
                        <IndexPage page={'presents'} />
                    </Route>
                    <Route path="/libresse/registration">
                        <IndexPage page={'registration'} />
                    </Route>
                    <Route path="/libresse/game">
                        <IndexPage page={'game'} />
                    </Route>
                    <Route path="/libresse/winners">
                        <IndexPage page={'winners'} />
                    </Route>
                </Switch>
            </div>
        </Router>
    );
}


export default App;